.form {
    display: flex;
    justify-content: center;
    width: 100%;
    border-radius: 5px;
    align-items: center;
}

.input {
    font-size: 1.2rem;
    font-family: monospace;
    height: 2rem;
    border: 2px solid #d4c7bc;
    text-align: center;
}

.button {
    min-width: 4rem;
    height: 2rem;
    padding: 0 1rem;
    margin: 1rem;
    border: 0;
    outline: 0;
    border-radius: 2px;
    color: #fff;
    font-size: 0.875rem;
    text-transform: uppercase;
    font-weight: 600;
    background-color: #7e4534;
    cursor: pointer;
}

.button:hover {
    background-color:	#7e4534;
}


.result-container {
    margin-top: 2rem;
    padding: 1rem;
    background-color: greenyellow;
    font-weight: 800;
  }
  
  .result-text {
    padding: 1rem;
    line-height: 1.5;
  }
.history-container {
    margin-top: 2rem;
    padding: 1rem;
    height: 15rem;
    overflow-y: scroll;
  }
  
.history-entry {
    padding: 0.5rem;
    font-family: monospace;
    line-height: 1.5;
  }
* {
  list-style: none;
  margin: 0;
  box-sizing: border-box;
  font-family: "Arial", sans-serif;
}

.app-container {
  display: flex;
  flex-wrap: wrap;
  height: 100vh;
  justify-content: center;
  text-align: center;
  font-style: normal !important;
  color: #333333;
  background: ghostwhite;
}

.header {
  width: 100%;
  line-height: 3rem;
  align-self: flex-start;
  background: #543936;
  color: #e9e9e9;
  font-family: monospace;
}

.header>p,
.footer>p {
  display: block;
  width: 100%;
  line-height: 4rem;
}

.main {
  width: 980px;
  background: #fff;
}

.container {
  padding: 2rem;
}

.title {
  padding-bottom: 1rem;
  color: 	#543936;
  font-family: monospace;
}

.footer {
  align-self: flex-end;
  width: 100%;
  background: 	#543936;
  color: #e9e9e9;
}


body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

